import { useState } from "react";
import { FaArrowLeft} from "react-icons/fa";
import { FaLink } from "react-icons/fa";
import { Link } from 'react-router-dom';
import axios from 'axios';
import validate from './LoginFormValidationRules';
import 'react-phone-number-input/style.css';
import PhoneInput from 'react-phone-number-input';

const Create = () => {
    /*const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [title, setTitle] = useState('');
    const [age, setAge] = useState('');
    const [dateOfBirth, setDateOfBirth] = useState('');
    const [image, setImage] = useState('');
    const [countryCode,setCountryCode] = useState('+234');
    const [phoneNumber, setPhoneNumber] = useState('');*/
    const [isPending, setIsPending] = useState(false);
    //const [fnerror, setFnError] = useState(false);
    const [errors, setErrors] = useState({});
    const [value, setValue] = useState({
        firstName: '',
        lastName: '',
        title: '',
        age: '',
        dateOfBirth: '',
        image: '',
        countryCode: '+234',
        phoneNumber: ''
    });

    const onChange = (event) =>{
        setValue(event.target.value);
    }

    const handleSubmit =(e) => {
        e.preventDefault();
        //const birthday = {firstName, lastName, title, age, dateOfBirth, image, countryCode, phoneNumber};
        setErrors(validate(value));
        setIsPending(true)
        axios.post("http://localhost:7000/app/create", 
            value.firstName,
            value.lastName,
            value.title,
            value.age,
            value.dateOfBirth,
            value.image,
            value.countryCode,
            value.phoneNumber
        )
        .then((response) =>{
            console.log('Birthday added');
            window.location.replace('/');
            setIsPending(false);
        })
        .catch((err)=>{
            alert(`Omo ment dey this code head: ${err}`);
            setIsPending(false)
        })
    };

    
    return ( 
        <main>
            <section className="container">
                <form onSubmit={handleSubmit}>
               <Link to='/' className='arrow'><FaArrowLeft/></Link> <h2>Add birthday</h2>
               <br />
               <label>
                    <h3 className= 'input name'>First Name</h3>
                </label>
                <input 
                type="text" 
                placeholder='Enter first name here'
                onChange={onChange}
                className= 'showError'
                value={value.firstName}
                />
                {errors.firstName && (
                    <p className="help is-danger" style={{color:'red'}}>{errors.firstName}</p>
                )}
                <label>
                    <h3 className= 'input name'>Last Name</h3>
                </label>
                <input 
                type="text"
                placeholder='Enter last name here'
                onChange={onChange}
                className= 'showError'
                value={value.lastName}
                />
                {errors.lastName && (
                    <p className="help is-danger" style={{color:'red'}}>{errors.lastName}</p>
                )}
                <label>
                    <h3 className= 'input title'>Title</h3>
                </label>
                <select
                value={value.title}
                onChange={onChange}
                >
                    <option value="Mr">Mr</option>
                    <option value="Mrs">Mrs</option>
                    <option value="Miss">Miss</option>
                    <option value="Bro">Bro</option>
                    <option value="Sis">Sis</option>
                </select>
                <label>
                    <h3 className='input age'>Age</h3>
                </label>
                <input 
                type="text" 
                placeholder='Enter age here'
                onChange={onChange}
                className= 'showError'
                value={value.age}
                />
                {errors.age && (
                    <p className="help is-danger" style={{color:'red'}}>{errors.age}</p>
                )}
                <label>
                    <h3 className= 'input dateOfBirth'>Date of birth</h3>
                </label>
                <input 
                type="date" 
                value={value.dateOfBirth}
                onChange={onChange}
                className= 'showError'
                />
                <label>
                    <h3 className= 'input phoneNumber'>Phone number</h3>
                </label>
               {/* <select
                className='country showError1'
                value={value.countryCode}
                onChange={onChange}
                >
                    <option value="+234">+234 Nigeria</option>
                    <option value="+234">+234 Nigeria</option>
                    <option value="+234">+234 Nigeria</option>
                    <option value="+234">+234 Nigeria</option>
                    <option value="+234">+234 Nigeria</option>
               </select>*/}
                <PhoneInput 
                className='phone showError correct'
                type="text" 
                placeholder='Enter your whatsapp phone number here'
                onChange={onChange}
                value={setValue}
                />
                {errors.phoneNumber && (
                    <p className="help is-danger" style={{color:'red'}}>{errors.phoneNumber}</p>
                )}
                <br />
                <label>
                    <h3 className='input image'>Image</h3>
                </label>
                <input 
                type="text"
                placeholder='Enter the link to your image file'
                onChange={onChange}
                className= 'showError'
                />
                {FaLink}
                {errors.image && (
                    <p className="help is-danger" style={{color:'red'}}>{errors.image}</p>
                )}
    
                {!isPending && <button>Add Birthday</button>}
                {isPending && <button disabled>Adding birthday...</button>}
                </form>
            </section>
        </main> 
    );
}
 
export default Create;