export default function validate(value) {
    let errors = {};
    if (!value.firstName){
      errors.firstName = 'First name is required';
    } else if(value.firstName >15){
      errors.firstName = 'First name is too long';
    }
    if (!value.lastName){
      errors.lastName = 'First name is required';
    } else if(value.lastName >15){
      errors.lastName = 'First name is too long';
    }
    if (!value.age){
      errors.age = 'Age is required';
    } else if(value.age >3){
      errors.age = 'Invalid age';
    } else if(value.age.includes('')){
      errors.age = 'Age is a number not a combination of letters'
    }
    if (!value.phoneNumber){
      errors.phoneNumber = 'Phone Numberis required';
    } else if(value.phoneNumber >11){
      errors.phoneNumber = 'Invalid phone number';
    } else if(value.phoneNumber < 10){
      errors.phoneNumber = 'Invalid phone number';
    } else if(value.phoneNumber.includes('')){
      errors.phoneNumber = 'Phon number is a number not a combination of letters'
    }
    return errors;
};