//import { FaFreeCodeCamp } from "react-icons/fa";
import { FaPlus } from 'react-icons/fa';
import React, { useState, useEffect } from 'react';
import List from './components/list';
import { Link } from 'react-router-dom';
import axios from 'axios';

function Main() {
  const url = 'http://localhost:7000/app/data';
  const [isPending, setIsPending] = useState(true);
  let [error, setError] = useState(false);

  const [info, setInfo] = useState([]);
  useEffect(()=>{
    axios.get(url)
    .then(res => {
      setInfo(res.data);
      setIsPending(false);
    })
    .catch(err => {
      console.log(err)
      setError(true)
    })
  }, [])

  return (
    <main>
      <section className='container eddy'>
        <h3>{info.length} Birthdays today<p className="plus"><FaPlus/> <p className="under"> <Link to= '/create' className='under'> Add birthday</Link></p></p> </h3>
        <List people={info} />
        { isPending && <div>Loading...</div> }
        { error && <div>{error}</div> }
        {/*<button onClick={() => setPeople([])}>clear all</button>*/}
      </section>
    </main>
  )
}

export default Main;
