import React from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import Create from "./components/create";
import Main from './main';
function App() {
  return (
    <Router>
       <Switch>
         <Route exact path='/'>
          <Main/>
         </Route>
         <Route path='/create'>
          <Create/>
         </Route>
       </Switch>
    </Router>
  )
}

export default App
