import React from 'react';



const List = ({ people }) => {
  return (
    <>
      {people.map((person) => {
        const { id, firstName, lastName, title, age, image, monthOfBirth, tateOfBirth } = person;
        let today= new Date();
        let month = today.getMonth();
        let date = today.getDate();
        console.log(month, date);
        if (monthOfBirth === month && tateOfBirth === date){
          console.log('True');
        }
        return (
          <article key={id} className='person'>
            <img src={image} alt={firstName +''+ lastName} />
            <div>
              <h4>{title}  {firstName} {lastName}</h4>
              <p>{age} years old</p>
            </div>
          </article>
        );
      })}
    </> 
  );
};

export default List